import React, { useState } from 'react';
import { CategoryBlock } from '../types';

interface CategoryBlockProps {
  category: CategoryBlock;
  children?: React.ReactNode; //    
  hasChildren?: boolean; //     
  isDragging?: boolean;
  isSelected?: boolean;
  isMemoBeingDragged?: boolean; //    
  isCategoryBeingDragged?: boolean; //    
  draggingCategoryId?: string | null; //    ID
  isDragHovered?: boolean; //    hover 
  isConnecting?: boolean;
  isDisconnectMode?: boolean;
  connectingFromId?: string | null;
  onUpdate: (category: CategoryBlock) => void;
  onDelete: (categoryId: string) => void;
  onToggleExpanded: (categoryId: string) => void;
  onDragStart?: (e: React.DragEvent, categoryId: string) => void;
  onDragEnd?: (e: React.DragEvent) => void;
  onDrop?: (e: React.DragEvent) => void;
  onDragOver?: (e: React.DragEvent) => void;
  onClick?: (categoryId: string, isShiftClick?: boolean) => void;
  onStartConnection?: (categoryId: string) => void;
  onConnectItems?: (fromId: string, toId: string) => void;
  onRemoveConnection?: (fromId: string, toId: string) => void;
  onPositionChange?: (categoryId: string, position: { x: number; y: number }) => void;
  onPositionDragEnd?: (categoryId: string) => void;
  onSizeChange?: (id: string, size: { width: number; height: number }) => void;
  onMoveToCategory?: (itemId: string, categoryId: string | null) => void;
  canvasScale?: number;
  canvasOffset?: { x: number; y: number };
  parentCategory?: CategoryBlock | null;
  currentPage?: any;
  isShiftPressed?: boolean;
}

const CategoryBlockComponent: React.FC<CategoryBlockProps> = ({
  category,
  children,
  hasChildren = false,
  isDragging = false,
  isSelected = false,
  isMemoBeingDragged = false,
  isCategoryBeingDragged = false,
  draggingCategoryId = null,
  isDragHovered = false,
  isConnecting = false,
  isDisconnectMode = false,
  connectingFromId = null,
  onUpdate,
  onDelete,
  onToggleExpanded,
  onDragStart,
  onDragEnd,
  onDrop,
  onDragOver,
  onClick,
  onStartConnection,
  onConnectItems,
  onRemoveConnection,
  onPositionChange,
  onPositionDragEnd,
  onSizeChange,
  onMoveToCategory,
  canvasScale = 1,
  canvasOffset = { x: 0, y: 0 },
  parentCategory = null,
  currentPage,
  isShiftPressed = false
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editTitle, setEditTitle] = useState(category.title);
  const [isHovered, setIsHovered] = useState(false);

  // MemoBlock    
  const [isDraggingPosition, setIsDraggingPosition] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const [dragMoved, setDragMoved] = useState(false);

  //     
  const lastUpdateTime = React.useRef<number>(0);
  const pendingPosition = React.useRef<{ x: number; y: number } | null>(null);

  const titleRef = React.useRef<HTMLInputElement>(null);
  const categoryRef = React.useRef<HTMLDivElement>(null);

  const handleTitleClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    setIsEditing(true);
    setEditTitle(category.title);
    setTimeout(() => titleRef.current?.focus(), 0);
  };

  const handleTitleSave = () => {
    setIsEditing(false);
    if (editTitle.trim() !== category.title) {
      onUpdate({
        ...category,
        title: editTitle.trim() || 'Untitled Category'
      });
    }
  };

  const handleTitleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleTitleSave();
    } else if (e.key === 'Escape') {
      setIsEditing(false);
      setEditTitle(category.title);
    }
  };

  const handleExpandToggle = (e: React.MouseEvent) => {
    e.stopPropagation();
    onToggleExpanded(category.id);
  };

  const handleClick = (e: React.MouseEvent) => {
    // MemoBlock  dragMoved  (isConnectionDragging )
    if (!dragMoved && !isEditing) {
      onClick?.(category.id, e.shiftKey);
    }
  };


  const handleDelete = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (window.confirm(` "${category.title}" ?    .`)) {
      onDelete(category.id);
    }
  };

  // MemoBlock   
  const handleMouseDown = (e: React.MouseEvent) => {
    if (e.button === 0 && !isConnecting && !isEditing) {
      setIsDraggingPosition(true);
      setDragMoved(false);
      setDragStart({
        x: e.clientX - (category.position.x * (canvasScale || 1) + (canvasOffset?.x || 0)),
        y: e.clientY - (category.position.y * (canvasScale || 1) + (canvasOffset?.y || 0))
      });
      e.preventDefault(); //    
    }
  };

  const handleConnectionPointMouseDown = (e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();

    if (!isConnecting) {
      onStartConnection?.(category.id);
    }
  };

  const handleConnectionPointMouseUp = (e: React.MouseEvent) => {
    e.stopPropagation();
    e.preventDefault();

    if (isConnecting && connectingFromId && connectingFromId !== category.id) {
      onConnectItems?.(connectingFromId, category.id);
    }
  };

  const handleMouseMove = React.useCallback((e: MouseEvent) => {
    if (isDraggingPosition && onPositionChange) {
      if (!dragMoved) {
        setDragMoved(true);
      }

      // MemoBlock   canvasScale canvasOffset 
      let newPosition = {
        x: (e.clientX - dragStart.x - (canvasOffset?.x || 0)) / (canvasScale || 1),
        y: (e.clientY - dragStart.y - (canvasOffset?.y || 0)) / (canvasScale || 1)
      };

      //    Shift      
      if (parentCategory && currentPage && !isShiftPressed) {
        const { calculateCategoryArea } = require('../utils/categoryAreaUtils');
        const parentArea = calculateCategoryArea(parentCategory, currentPage);

        if (parentArea) {
          const catWidth = category.size?.width || 200;
          const catHeight = category.size?.height || 80;

          //     
          const minX = parentArea.left;
          const minY = parentArea.top;
          const maxX = parentArea.right - catWidth;
          const maxY = parentArea.bottom - catHeight;

          newPosition.x = Math.max(minX, Math.min(maxX, newPosition.x));
          newPosition.y = Math.max(minY, Math.min(maxY, newPosition.y));
        }
      }

      //       (50ms )
      const now = Date.now();
      pendingPosition.current = newPosition;

      if (now - lastUpdateTime.current >= 50) {
        onPositionChange(category.id, newPosition);
        lastUpdateTime.current = now;
      }
    }
  }, [isDraggingPosition, onPositionChange, dragMoved, dragStart, canvasOffset, canvasScale, category.id, category.size, parentCategory, currentPage, isShiftPressed]);

  const handleMouseUp = React.useCallback(() => {
    if (isDraggingPosition && onPositionChange) {
      //       (    )
      if (pendingPosition.current) {
        onPositionChange(category.id, pendingPosition.current);
      }

      //    
      onPositionDragEnd?.(category.id);

      //  
      pendingPosition.current = null;
      lastUpdateTime.current = 0;
    }
    setIsDraggingPosition(false);
  }, [isDraggingPosition, onPositionChange, onPositionDragEnd, category.id]);

  React.useEffect(() => {
    if (isDraggingPosition) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      return () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };
    }
  }, [isDraggingPosition, handleMouseMove, handleMouseUp]);

  // ResizeObserver    (  )
  React.useEffect(() => {
    if (!categoryRef.current || !onSizeChange) return;

    let timeoutId: NodeJS.Timeout;

    const updateSize = () => {
      //        
      if (isDraggingPosition || isDragHovered) {
        return;
      }

      if (categoryRef.current) {
        const rect = categoryRef.current.getBoundingClientRect();
        // 0     (    )
        if (rect.width < 10 || rect.height < 10) {
          return;
        }

        // scale     
        const newSize = {
          width: Math.round(rect.width / canvasScale),
          height: Math.round(rect.height / canvasScale)
        };

        //       (5px  )
        if (!category.size ||
            Math.abs(category.size.width - newSize.width) > 5 ||
            Math.abs(category.size.height - newSize.height) > 5) {
          // : 100ms  
          clearTimeout(timeoutId);
          timeoutId = setTimeout(() => {
            onSizeChange(category.id, newSize);
          }, 100);
        }
      }
    };

    //      
    timeoutId = setTimeout(updateSize, 50);

    const resizeObserver = new ResizeObserver(() => {
      // ResizeObserver  
      clearTimeout(timeoutId);
      timeoutId = setTimeout(updateSize, 100);
    });

    if (categoryRef.current) {
      resizeObserver.observe(categoryRef.current);
    }

    return () => {
      clearTimeout(timeoutId);
      resizeObserver.disconnect();
    };
  }, [category.id, category.title, category.tags, category.children, onSizeChange, canvasScale, isDraggingPosition, isDragHovered]);

  //     (    )
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    onDragOver?.(e);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();

    //   ID 
    try {
      const dragDataStr = e.dataTransfer.getData('text/plain');

      const dragData = JSON.parse(dragDataStr);

      if (dragData.id && onMoveToCategory) {
        onMoveToCategory(dragData.id, category.id);
      } else {
      }
    } catch (error) {
      console.error(' Error parsing drag data:', error);
    }

    onDrop?.(e);
  };

  //    -   + /    
  //  (isDragHovered)     
  const isHighlighted = isDragHovered;
  const categoryStyle: React.CSSProperties = {
    width: '100%', //   
    minWidth: '200px',
    minHeight: '80px',
    backgroundColor: isHighlighted ? '#581c87' : (isSelected ? '#7c3aed' : '#8b5cf6'),
    border: isHighlighted ? '3px solid #4c1d95' : (isSelected ? '2px solid #6d28d9' : '1px solid #7c3aed'),
    borderRadius: '8px',
    padding: '16px',
    boxShadow: isDragging ? '0 8px 16px rgba(0,0,0,0.3)' : (isHighlighted ? '0 6px 20px rgba(139, 92, 246, 0.6)' : '0 2px 8px rgba(0,0,0,0.1)'),
    cursor: isDraggingPosition ? 'grabbing' : 'grab',
    opacity: isDragging ? 0.7 : (isHighlighted ? 0.8 : 1),
    transition: isDraggingPosition ? 'none' : 'all 0.1s ease',
    fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
    userSelect: 'none',
    zIndex: 5,
    boxSizing: 'border-box',
    transform: isHighlighted ? 'scale(1.3)' : 'scale(1)'
  };

  const headerStyle: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    marginBottom: category.isExpanded && children ? '12px' : '0',
    minHeight: '24px',
    width: '100%', //   
    overflow: 'hidden' //   
  };

  const expandButtonStyle: React.CSSProperties = {
    background: 'none',
    border: 'none',
    cursor: 'pointer',
    padding: '4px',
    marginRight: '8px',
    fontSize: '14px',
    color: 'white',
    display: hasChildren ? 'flex' : 'none',
    alignItems: 'center',
    justifyContent: 'center'
  };


  const titleStyle: React.CSSProperties = {
    flex: 1,
    fontSize: '16px',
    fontWeight: 600,
    color: 'white',
    backgroundColor: 'transparent',
    border: isEditing ? '1px solid #1976d2' : 'none',
    borderRadius: '4px',
    padding: '4px 8px',
    outline: 'none',
    minWidth: 0, // flex    
    maxWidth: '100%', //    
    boxSizing: 'border-box' //     
  };

  const controlsStyle: React.CSSProperties = {
    display: isHovered ? 'flex' : 'none',
    alignItems: 'center',
    marginLeft: '8px'
  };

  const deleteButtonStyle: React.CSSProperties = {
    background: 'none',
    border: 'none',
    cursor: 'pointer',
    padding: '4px 8px',
    fontSize: '14px',
    color: '#f44336',
    borderRadius: '4px'
  };

  const childrenContainerStyle: React.CSSProperties = {
    marginLeft: '16px',
    paddingLeft: '12px',
    borderLeft: '2px solid #e0e0e0',
    display: category.isExpanded ? 'block' : 'none'
  };

  const tagsStyle: React.CSSProperties = {
    display: 'flex',
    flexWrap: 'wrap',
    gap: '6px',
    marginTop: category.tags.length > 0 ? '8px' : '0'
  };

  const tagStyle: React.CSSProperties = {
    backgroundColor: '#e3f2fd',
    color: '#1976d2',
    padding: '4px 8px',
    borderRadius: '12px',
    fontSize: '12px',
    fontWeight: 500
  };

  return (
    <div style={{
      position: 'absolute',
      left: category.position.x,
      top: category.position.y,
      width: category.size?.width ? `${category.size.width}px` : 'auto',
      height: 'auto',
      //  (    )      
      display: ((hasChildren && category.isExpanded) || parentCategory) ? 'none' : 'block'
    }}>
      {/*    */}
      <div
        ref={categoryRef}
        data-category-block="true"
        data-category-id={category.id}
        draggable={true}
        onDragStart={(e) => {
          e.dataTransfer.setData('text/plain', JSON.stringify({ type: 'category', id: category.id }));
          onDragStart?.(e, category.id);
        }}
        onDragEnd={(e) => {
          onDragEnd?.(e);
        }}
        style={categoryStyle}
        onDragOver={handleDragOver}
        onDrop={handleDrop}
        onClick={handleClick}
        onMouseDown={handleMouseDown}
        onMouseUp={(e) => {
          //        
          if (isConnecting && connectingFromId && connectingFromId !== category.id) {
            e.stopPropagation();
            onConnectItems?.(connectingFromId, category.id);
          }
        }}
        onMouseEnter={() => {
          setIsHovered(true);
        }}
        onMouseLeave={() => {
          setIsHovered(false);
        }}
      >
      <div style={headerStyle}>
        <button
          style={expandButtonStyle}
          onClick={handleExpandToggle}
          title={category.isExpanded ? '' : ''}
        >
          {category.isExpanded ? '' : ''}
        </button>


        {isEditing ? (
          <input
            ref={titleRef}
            style={titleStyle}
            value={editTitle}
            onChange={(e) => setEditTitle(e.target.value)}
            onBlur={handleTitleSave}
            onKeyDown={handleTitleKeyDown}
            placeholder=" ..."
          />
        ) : (
          <div
            style={titleStyle}
            onClick={handleTitleClick}
            title=" "
          >
            {category.title}
          </div>
        )}

        <div style={controlsStyle}>
          <button
            style={deleteButtonStyle}
            onClick={handleDelete}
            title=" "
          >
            
          </button>
        </div>
      </div>

      {/*   */}
      {category.tags.length > 0 && (
        <div style={tagsStyle}>
          {category.tags.map((tag, index) => (
            <span key={index} style={tagStyle}>
              #{tag}
            </span>
          ))}
        </div>
      )}

      {/*   */}
      {category.isExpanded && children && (
        <div style={childrenContainerStyle}>
          {children}
        </div>
      )}

      {/* MemoBlock  4  */}
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          top: -8,
          left: '50%',
          transform: 'translateX(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          bottom: -8,
          left: '50%',
          transform: 'translateX(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          left: -8,
          top: '50%',
          transform: 'translateY(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>
      <div
        onMouseDown={handleConnectionPointMouseDown}
        onMouseUp={handleConnectionPointMouseUp}
        onClick={(e) => {
          e.stopPropagation();
          e.preventDefault();
        }}
        style={{
          position: 'absolute',
          right: -8,
          top: '50%',
          transform: 'translateY(-50%)',
          width: 16,
          height: 16,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          cursor: 'crosshair',
          zIndex: 15
        }}
      >
        <div style={{
          width: 8,
          height: 8,
          backgroundColor: isConnecting && connectingFromId === category.id ? '#ef4444' : '#8b5cf6',
          borderRadius: '50%',
          border: '2px solid white',
          boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
        }} />
      </div>

      {/*    */}
      {category.isExpanded && (
        <div
          style={{
            minHeight: '20px',
            textAlign: 'center',
            color: 'white',
            fontSize: '12px',
            padding: '8px',
            border: '1px dashed white',
            borderRadius: '4px',
            marginTop: '12px'
          }}
        >
             
        </div>
      )}
      </div>
    </div>
  );
};

export default CategoryBlockComponent;