import React, { useState, useRef, useEffect } from 'react';
import { TextBlock, ContentBlockType, ImportanceLevel, ImportanceRange } from '../../types';
import BlockSelector from '../BlockSelector';

//     
const getImportanceStyle = (level: ImportanceLevel) => {
  switch (level) {
    case 'critical':
      return { backgroundColor: '#ffcdd2', color: '#000' }; //   - 
    case 'important':
      return { backgroundColor: '#ffcc80', color: '#000' }; //   - 
    case 'opinion':
      return { backgroundColor: '#e1bee7', color: '#000' }; //   - 
    case 'reference':
      return { backgroundColor: '#81d4fa', color: '#000' }; //   - 
    case 'question':
      return { backgroundColor: '#fff59d', color: '#000' }; //   - 
    case 'idea':
      return { backgroundColor: '#c8e6c9', color: '#000' }; //   - 
    case 'data':
      return { backgroundColor: '#ffab91', color: '#000' }; //   - 
    default:
      return {};
  }
};

const IMPORTANCE_LABELS = {
  critical: ' ',
  important: ' ',
  opinion: ' ',
  reference: ' ',
  question: ' ',
  idea: ' ',
  data: ' ',
  none: ' '
};

interface TextBlockProps {
  block: TextBlock;
  isEditing?: boolean;
  onUpdate?: (block: TextBlock) => void;
  onConvertToBlock?: (blockType: ContentBlockType) => void;
  onCreateNewBlock?: (afterBlockId: string, content: string) => void;
  onDeleteBlock?: (blockId: string) => void;
  onFocusPrevious?: (blockId: string) => void;
  onFocusNext?: (blockId: string) => void;
  onMergeWithPrevious?: (blockId: string, content: string) => void;
  onSaveToHistory?: () => void;
  activeImportanceFilters?: Set<ImportanceLevel>;
  showGeneralContent?: boolean;
  onResetFilters?: () => void; //     
}

const TextBlockComponent: React.FC<TextBlockProps> = ({
  block,
  isEditing = false,
  onUpdate,
  onConvertToBlock,
  onCreateNewBlock,
  onDeleteBlock,
  onFocusPrevious,
  onFocusNext,
  onMergeWithPrevious,
  onSaveToHistory,
  activeImportanceFilters,
  showGeneralContent,
  onResetFilters
}) => {
  //           
  const isDefaultFilterState = () => {
    const allLevels: ImportanceLevel[] = ['critical', 'important', 'opinion', 'reference', 'question', 'idea', 'data'];

    // activeImportanceFilters     , showGeneralContent true 
    return (!activeImportanceFilters ||
            (activeImportanceFilters.size === allLevels.length &&
             allLevels.every(level => activeImportanceFilters.has(level)))) &&
           showGeneralContent !== false;
  };

  const canEdit = isDefaultFilterState();

  //    
  const getFilteredText = () => {
    if (!block.content) return '';
    if (canEdit || (!activeImportanceFilters && showGeneralContent !== false)) {
      return content; //  
    }

    if (!block.importanceRanges || block.importanceRanges.length === 0) {
      return showGeneralContent === false ? '' : content;
    }

    //   
    const ranges = [...block.importanceRanges].sort((a, b) => a.start - b.start);
    let result = '';
    let lastIndex = 0;

    ranges.forEach(range => {
      //   ( )
      if (range.start > lastIndex && showGeneralContent !== false) {
        result += block.content.substring(lastIndex, range.start);
      }

      //   ( )
      if (!activeImportanceFilters || activeImportanceFilters.has(range.level)) {
        result += block.content.substring(range.start, range.end);
      }

      lastIndex = range.end;
    });

    //   ( )
    if (lastIndex < block.content.length && showGeneralContent !== false) {
      result += block.content.substring(lastIndex);
    }

    return result;
  };
  const [content, setContent] = useState(block.content);
  const [showBlockSelector, setShowBlockSelector] = useState(false);
  const [selectorPosition, setSelectorPosition] = useState({ x: 0, y: 0 });
  const [slashQuery, setSlashQuery] = useState('');
  const [slashStartPos, setSlashStartPos] = useState(0);
  const [isFocused, setIsFocused] = useState(false);
  const [showImportanceMenu, setShowImportanceMenu] = useState(false);
  const [importanceMenuPosition, setImportanceMenuPosition] = useState({ x: 0, y: 0 });
  const [selectedRange, setSelectedRange] = useState<{ start: number; end: number } | null>(null);
  const [, forceUpdate] = useState({});
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  //        
  useEffect(() => {
    if (block.content !== content) {
      setContent(block.content);
    }
  }, [block.content]);


  //    -    

  // block      ( importanceRanges)
  useEffect(() => {
    forceUpdate({});
    // importanceRanges       
    if (block.importanceRanges && block.importanceRanges.length > 0) {
      setTimeout(() => {
        forceUpdate({});
      }, 50);
    }
  }, [block]);

  // importanceRanges  
  useEffect(() => {
    if (block.importanceRanges && block.importanceRanges.length > 0) {
      forceUpdate({});
      setTimeout(() => forceUpdate({}), 10);
    }
  }, [block.importanceRanges]);

  //     
  useEffect(() => {
    if (isEditing && textareaRef.current) {

      //    content  
      if (textareaRef.current.value !== content) {
        textareaRef.current.value = content;
      }

      //  content  content  
      if (content !== block.content) {
        setContent(block.content);
        if (textareaRef.current) {
          textareaRef.current.value = block.content;
        }
      }
    }
  }, [isEditing]);

  //     
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as Element;
      //      
      if (showImportanceMenu && !target.closest('[data-importance-menu]')) {
        setShowImportanceMenu(false);
        setSelectedRange(null);
      }
    };

    if (showImportanceMenu) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showImportanceMenu]);

  //     ( )
  useEffect(() => {
    const textarea = textareaRef.current;
    if (textarea) {
      const adjustHeight = () => {
        textarea.style.height = '24px'; //    

        //   24px 
        if (content.trim() === '') {
          return;
        }

        //   scrollHeight ,    24px 
        if (content.includes('\n') || textarea.scrollHeight > 24) {
          textarea.style.height = `${textarea.scrollHeight}px`;
        }
      };
      adjustHeight();
    }
  }, [content]);

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (showBlockSelector) {
      if (e.key === 'Escape') {
        setShowBlockSelector(false);
        setSlashQuery('');
        return;
      }
      return;
    }

    // Enter     (Shift+Enter )
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      
      //     
      const textarea = e.currentTarget;
      const cursorPos = textarea.selectionStart || 0;
      const beforeCursor = content.substring(0, cursorPos);
      const afterCursor = content.substring(cursorPos);
      
      //      
      const updatedContent = beforeCursor;
      setContent(updatedContent);
      if (onUpdate) {
        onUpdate({ ...block, content: updatedContent });
      }
      
      //     (  )
      if (onCreateNewBlock) {
        onCreateNewBlock(block.id, afterCursor);
        // blur  -      
      } else if (onConvertToBlock) {
        onConvertToBlock('text');
      }
    }

    if (e.key === 'Escape') {
      // Escape      
      if (content !== block.content && onUpdate) {
        onUpdate({ ...block, content, importanceRanges: block.importanceRanges });
      }
      if (onSaveToHistory) {
        onSaveToHistory();
      }
      if (textareaRef.current) {
        textareaRef.current.blur();
      }
    }

    //   -  
    if (e.key === 'Backspace') {
      const textarea = textareaRef.current;
      if (!textarea) return;

      const selectionStart = textarea.selectionStart;
      const selectionEnd = textarea.selectionEnd;
      const currentContent = textarea.value || '';


      //       (  )
      if (selectionStart !== selectionEnd) {
        e.stopPropagation();
        return;
      }

      //      -   
      if (selectionStart === 0) {
        e.preventDefault();
        e.stopPropagation();

        //       
        if (onMergeWithPrevious) {
          onMergeWithPrevious(block.id, currentContent);
        } else {
          //    (       )
          //     
          if (currentContent === '') {
            if (onDeleteBlock) {
              onDeleteBlock(block.id);
            }
          } else {
            //         ( )
          }
        }
        return;
      }

      //     -   
      e.stopPropagation();
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newContent = e.target.value;
    const cursorPos = e.target.selectionStart || 0;
    
    //   
    const textarea = e.target;
    textarea.style.height = '24px'; //    

    //     scrollHeight 24px  
    if (newContent.trim() !== '' && (newContent.includes('\n') || textarea.scrollHeight > 24)) {
      textarea.style.height = `${textarea.scrollHeight}px`;
    }
    
    //   
    if (newContent[cursorPos - 1] === '/') {
      const textarea = e.target;
      const rect = textarea.getBoundingClientRect();
      
      //    
      const lineHeight = 20;
      const lines = newContent.substring(0, cursorPos).split('\n');
      const currentLine = lines.length - 1;
      const charInLine = lines[lines.length - 1].length;
      
      setSelectorPosition({
        x: rect.left + charInLine * 8,
        y: rect.top + currentLine * lineHeight + lineHeight + window.scrollY
      });
      
      setSlashStartPos(cursorPos - 1);
      setSlashQuery('');
      setShowBlockSelector(true);
    } else if (showBlockSelector) {
      //    
      const slashPart = newContent.substring(slashStartPos);
      if (slashPart.startsWith('/')) {
        const query = slashPart.substring(1).split(/\s/)[0];
        setSlashQuery(query);
      } else {
        setShowBlockSelector(false);
        setSlashQuery('');
      }
    }
    
    setContent(newContent);
  };

  const handleBlockSelect = (blockType: ContentBlockType) => {
    //    
    const beforeSlash = content.substring(0, slashStartPos);
    const afterSlash = content.substring(slashStartPos + slashQuery.length + 1);
    const cleanContent = beforeSlash + afterSlash;
    
    //        
    setContent(cleanContent);
    if (onUpdate) {
      onUpdate({ ...block, content: cleanContent });
    }
    
    //     (   )
    if (onConvertToBlock) {
      onConvertToBlock(blockType);
    }
    
    setShowBlockSelector(false);
    setSlashQuery('');
  };

  const handleCloseBlockSelector = () => {
    setShowBlockSelector(false);
    setSlashQuery('');
  };

  const handleFocus = () => {
    setIsFocused(true);
  };

  const handleBlur = () => {
    if (!showBlockSelector) {
      setIsFocused(false);

      //   
      if (content !== block.content && onUpdate) {
        onUpdate({ ...block, content, importanceRanges: block.importanceRanges });

        //      
        if (onSaveToHistory) {
          setTimeout(() => onSaveToHistory(), 100); //      
        }
      }
    }
  };

  const handleClick = (e: React.MouseEvent) => {
    if (isEditing && textareaRef.current) {
      textareaRef.current.focus();
    }
  };

  //   
  const handleTextSelection = (e: React.MouseEvent) => {
    if (!isEditing) return;
    
    setTimeout(() => {
      const textarea = textareaRef.current;
      
      if (textarea) {
        const start = textarea.selectionStart;
        const end = textarea.selectionEnd;
        
        
        if (start !== end && end > start) {
          setSelectedRange({ start, end });
          setImportanceMenuPosition({
            x: e.clientX,
            y: e.clientY - 10
          });
          setShowImportanceMenu(true);
        } else {
          setShowImportanceMenu(false);
          setSelectedRange(null);
        }
      }
    }, 10);
  };

  //  
  const applyImportance = (level: ImportanceLevel) => {
    if (!selectedRange) {
      return;
    }
    
    
    const ranges = block.importanceRanges || [];
    const newRange: ImportanceRange = {
      start: selectedRange.start,
      end: selectedRange.end,
      level: level
    };
    
    let updatedRanges: ImportanceRange[];

    if (level === 'none') {
      //  :       
      updatedRanges = [];

      ranges.forEach(range => {
        //     
        if (range.end <= selectedRange.start || range.start >= selectedRange.end) {
          updatedRanges.push(range);
        } else {
          //   
          //   
          if (range.start < selectedRange.start) {
            updatedRanges.push({
              start: range.start,
              end: selectedRange.start,
              level: range.level
            });
          }

          //   
          if (range.end > selectedRange.end) {
            updatedRanges.push({
              start: selectedRange.end,
              end: range.end,
              level: range.level
            });
          }
        }
      });
    } else {
      //  :     ,     
      updatedRanges = [];

      ranges.forEach(range => {
        //     
        if (range.end <= selectedRange.start || range.start >= selectedRange.end) {
          updatedRanges.push(range);
        } else {
          //       
          //   
          if (range.start < selectedRange.start) {
            updatedRanges.push({
              start: range.start,
              end: selectedRange.start,
              level: range.level
            });
          }

          //   
          if (range.end > selectedRange.end) {
            updatedRanges.push({
              start: selectedRange.end,
              end: range.end,
              level: range.level
            });
          }
          //       
        }
      });

      //    
      updatedRanges.push(newRange);
    }
    

    //      React    
    const freshUpdatedRanges = updatedRanges.map(range => ({ ...range }));

    const updatedBlock = {
      ...block,
      content: content, //    content  
      importanceRanges: freshUpdatedRanges
    };

    //  content   (importanceRanges props )
    setContent(updatedBlock.content);

    if (onUpdate) {
      onUpdate(updatedBlock);
    }

    //     
    if (onSaveToHistory) {
      setTimeout(() => onSaveToHistory(), 50); //     
    }

    //     
    setShowImportanceMenu(false);
    setSelectedRange(null);

    //   (  )
    forceUpdate({});

    //  DOM    
    setTimeout(() => {
      forceUpdate({});
      // DOM   
      if (textareaRef.current && textareaRef.current.parentElement) {
        const backgroundDiv = textareaRef.current.parentElement.querySelector('div[style*="position: absolute"]');
        if (backgroundDiv && !backgroundDiv.textContent) {
          //  div      
          forceUpdate({});
        }
      }
    }, 10);

    setTimeout(() => forceUpdate({}), 50);
    setTimeout(() => forceUpdate({}), 100);
    setTimeout(() => forceUpdate({}), 200);

    //  
    setTimeout(() => {
      if (textareaRef.current) {
        textareaRef.current.focus();
      }
    }, 100);
  };

  //     
  const renderFilteredStyledText = (text: string, importanceRanges?: ImportanceRange[], activeFilters?: Set<ImportanceLevel>, showGeneral?: boolean) => {
    if (!importanceRanges || importanceRanges.length === 0) {
      //         /
      return showGeneral === false ? '' : text;
    }

    const ranges = [...importanceRanges].sort((a, b) => a.start - b.start);
    const parts: Array<{ text: string; level?: ImportanceLevel }> = [];
    let lastIndex = 0;

    ranges.forEach(range => {
      //   ( )
      if (range.start > lastIndex) {
        parts.push({ text: text.substring(lastIndex, range.start) });
      }

      //   ( )
      parts.push({
        text: text.substring(range.start, range.end),
        level: range.level
      });

      lastIndex = range.end;
    });

    //   ( )
    if (lastIndex < text.length) {
      parts.push({ text: text.substring(lastIndex) });
    }

    return parts.map((part, index) => {
      //   -    null    
      if (part.level) {
        //      
        if (activeFilters && !activeFilters.has(part.level)) {
          return null; //    
        }
      } else {
        //    showGeneral  
        if (showGeneral === false) {
          return null; //   
        }
      }

      const importanceStyle = part.level ? getImportanceStyle(part.level) : {};
      return (
        <span
          key={index}
          style={part.level ? {
            backgroundColor: importanceStyle.backgroundColor,
            padding: '1px 0px',
            borderRadius: '2px',
            fontWeight: '500',
            color: 'transparent', //  
            margin: '0',
            display: 'inline'
          } : {
            color: 'rgba(0,0,0,0.05)' //    
          }}
        >
          {part.text}
        </span>
      );
    }).filter(Boolean); // null  
  };

  //    
  const renderStyledText = (text: string, ranges: ImportanceRange[] = block.importanceRanges || []) => {

    //    
    if (ranges && ranges.length > 0) {
      ranges.forEach((range, index) => {
      });
    }

    if (!ranges || ranges.length === 0) {
      return text;
    }

    const sortedRanges = [...ranges].sort((a, b) => a.start - b.start);
    const parts: Array<{ text: string; level?: ImportanceLevel }> = [];
    let lastIndex = 0;

    sortedRanges.forEach(range => {
      //   ( )
      if (lastIndex < range.start) {
        parts.push({ text: text.substring(lastIndex, range.start) });
      }
      
      //   
      parts.push({ 
        text: text.substring(range.start, range.end), 
        level: range.level 
      });
      
      lastIndex = range.end;
    });
    
    //   ( )
    if (lastIndex < text.length) {
      parts.push({ text: text.substring(lastIndex) });
    }
    
    return parts.map((part, index) => {
      const importanceStyle = part.level ? getImportanceStyle(part.level) : {};

      return (
        <span
          key={index}
          style={part.level ? {
            backgroundColor: importanceStyle.backgroundColor,
            padding: '1px 0px',
            borderRadius: '2px',
            fontWeight: '500',
            color: 'transparent', //  
            margin: '0',
            display: 'inline'
          } : {
            color: 'rgba(0,0,0,0.05)' //    
          }}
        >
          {part.text}
        </span>
      );
    });
  };

  if (isEditing) {
    //  
    return (
      <>
        <div style={{
          marginBottom: '0px',
          position: 'relative',
          minHeight: '24px',
          display: 'flex',
          alignItems: 'center'
        }}>
          {/*     -   */}
          {block.importanceRanges && block.importanceRanges.length > 0 && (
            <div
              style={{
                position: 'absolute',
                top: '2px',
                left: '0px',
                right: '0px',
                bottom: '0px',
                fontFamily: 'inherit',
                fontSize: '14px',
                lineHeight: '1.4',
                padding: '0px',
                pointerEvents: 'none',
                zIndex: 1,
                whiteSpace: 'pre-wrap',
                wordBreak: 'break-word',
                color: 'transparent', //  ,  
                userSelect: 'none' //  
              }}
            >
              {(activeImportanceFilters || showGeneralContent !== undefined) ?
                renderFilteredStyledText(content, block.importanceRanges, activeImportanceFilters, showGeneralContent) :
                renderStyledText(content, block.importanceRanges)}
            </div>
          )}
          <textarea
            ref={textareaRef}
            value={canEdit ? content : getFilteredText()}
            onChange={handleInputChange}
            onFocus={handleFocus}
            onBlur={handleBlur}
            onKeyDown={handleKeyDown}
            onClick={handleClick}
            onMouseUp={handleTextSelection}
            data-block-id={block.id}
            disabled={!canEdit}
            placeholder={content === '' && isFocused ? "'/'      " : ''}
            style={{
              position: 'relative',
              zIndex: 2,
              width: '100%',
              minHeight: '24px',
              border: 'none',
              borderRadius: '4px',
              padding: '1px 0',
              fontFamily: 'inherit',
              fontSize: '14px',
              resize: 'none',
              overflow: 'hidden',
              backgroundColor: 'transparent',
              outline: 'none',
              lineHeight: '1.4',
              transition: 'all 0.15s ease',
              color: 'inherit',
              cursor: !canEdit ? 'not-allowed' : 'text'
            }}
            onMouseEnter={(e) => {
              //       
              if (!isFocused && (!block.importanceRanges || block.importanceRanges.length === 0)) {
                e.currentTarget.style.backgroundColor = '#f8f9fa';
              }
            }}
            onMouseLeave={(e) => {
              //       
              if (!isFocused && (!block.importanceRanges || block.importanceRanges.length === 0)) {
                e.currentTarget.style.backgroundColor = 'transparent';
              }
            }}
          />
        </div>
        
        <BlockSelector
          isVisible={showBlockSelector}
          position={selectorPosition}
          searchQuery={slashQuery}
          onSelect={handleBlockSelect}
          onClose={handleCloseBlockSelector}
        />
        
        {/*   */}
        {showImportanceMenu && (
          <div
            data-importance-menu
            onMouseDown={(e) => e.preventDefault()} //   
            style={{
              position: 'fixed',
              left: importanceMenuPosition.x,
              top: importanceMenuPosition.y,
              backgroundColor: '#ffffff',
              border: '1px solid #e0e0e0',
              borderRadius: '8px',
              boxShadow: '0 4px 12px rgba(0, 0, 0, 0.15)',
              zIndex: 1000,
              padding: '4px',
              minWidth: '140px'
            }}
          >
            {Object.entries(IMPORTANCE_LABELS).map(([level, label]) => (
              <button
                key={level}
                onClick={(e) => {
                  e.stopPropagation();
                  applyImportance(level as ImportanceLevel);
                }}
                style={{
                  display: 'block',
                  width: '100%',
                  padding: '6px 8px',
                  border: 'none',
                  backgroundColor: 'transparent',
                  textAlign: 'left',
                  cursor: 'pointer',
                  borderRadius: '4px',
                  fontSize: '12px',
                  fontFamily: 'inherit'
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.backgroundColor = '#f5f5f5';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.backgroundColor = 'transparent';
                }}
              >
                {label}
              </button>
            ))}
          </div>
        )}
      </>
    );
  }

  //      (MemoBlock  )
  const renderFilteredHighlightedText = (text: string, importanceRanges?: ImportanceRange[], activeFilters?: Set<ImportanceLevel>, showGeneral?: boolean) => {
    if (!importanceRanges || importanceRanges.length === 0) {
      //         /
      return showGeneral === false ? '' : text;
    }

    const ranges = [...importanceRanges].sort((a, b) => a.start - b.start);
    const parts: Array<{ text: string; level?: ImportanceLevel }> = [];
    let lastIndex = 0;

    ranges.forEach(range => {
      //   ( )
      if (range.start > lastIndex) {
        parts.push({ text: text.substring(lastIndex, range.start) });
      }

      //   ( )
      parts.push({
        text: text.substring(range.start, range.end),
        level: range.level
      });

      lastIndex = range.end;
    });

    //   ( )
    if (lastIndex < text.length) {
      parts.push({ text: text.substring(lastIndex) });
    }

    return parts.map((part, index) => {
      //   -    null    
      if (part.level) {
        //      
        if (activeFilters && !activeFilters.has(part.level)) {
          return null; //    
        }
      } else {
        //    showGeneral  
        if (showGeneral === false) {
          return null; //   
        }
      }

      const style = part.level ? getImportanceStyle(part.level) : {};
      return (
        <span key={index} style={part.level ? {
          ...style,
          padding: '1px 2px',
          borderRadius: '2px',
          fontWeight: '500',
          margin: '0 1px'
        } : {}}>
          {part.text}
        </span>
      );
    }).filter(Boolean); // null  
  };

  //       
  const renderStyledTextForReadMode = (text: string, ranges: ImportanceRange[] = block.importanceRanges || []) => {

    //    
    if (ranges && ranges.length > 0) {
      ranges.forEach((range, index) => {
      });
    }

    if (!ranges || ranges.length === 0) {
      return text;
    }

    const sortedRanges = [...ranges].sort((a, b) => a.start - b.start);
    const parts: Array<{ text: string; level?: ImportanceLevel }> = [];
    let lastIndex = 0;

    sortedRanges.forEach(range => {
      //   ( )
      if (lastIndex < range.start) {
        parts.push({ text: text.substring(lastIndex, range.start) });
      }
      
      //   
      parts.push({ 
        text: text.substring(range.start, range.end), 
        level: range.level 
      });
      
      lastIndex = range.end;
    });
    
    //   ( )
    if (lastIndex < text.length) {
      parts.push({ text: text.substring(lastIndex) });
    }
    
    return parts.map((part, index) => (
      <span 
        key={index}
        style={part.level ? {
          ...getImportanceStyle(part.level),
          padding: '1px 2px',
          borderRadius: '2px',
          fontWeight: '500',
          margin: '0 1px'
        } : {}}
      >
        {part.text}
      </span>
    ));
  };

  //  
  return (
    <div
      onClick={handleClick}
      style={{
        display: 'flex',
        alignItems: 'center',
        padding: '1px 0',
        borderRadius: '4px',
        cursor: 'text',
        minHeight: '24px',
        whiteSpace: 'pre-wrap',
        wordBreak: 'break-word',
        lineHeight: '1.4',
        fontSize: '14px'
      }}
    >
{block.content ? (
        (activeImportanceFilters || showGeneralContent !== undefined) ?
          renderFilteredHighlightedText(block.content, block.importanceRanges, activeImportanceFilters, showGeneralContent) :
          renderStyledTextForReadMode(block.content, block.importanceRanges)
      ) : (
        <span style={{ color: '#999', fontStyle: 'italic' }}> </span>
      )}
    </div>
  );
};

export default TextBlockComponent;