import React from 'react';
import { Page, MemoBlock, ImportanceLevel } from '../types';
import Resizer from './Resizer';

type SearchCategory = 'all' | 'title' | 'tags' | 'content';

//     
const getImportanceStyle = (level: ImportanceLevel) => {
  switch (level) {
    case 'critical':
      return { backgroundColor: '#ffcdd2', color: '#000' }; //   - 
    case 'important':
      return { backgroundColor: '#ffcc80', color: '#000' }; //   - 
    case 'opinion':
      return { backgroundColor: '#e1bee7', color: '#000' }; //   - 
    case 'reference':
      return { backgroundColor: '#81d4fa', color: '#000' }; //   - 
    case 'question':
      return { backgroundColor: '#fff59d', color: '#000' }; //   - 
    case 'idea':
      return { backgroundColor: '#c8e6c9', color: '#000' }; //   - 
    case 'data':
      return { backgroundColor: '#ffab91', color: '#000' }; //   - 
    default:
      return {};
  }
};

interface LeftPanelProps {
  pages: Page[];
  currentPageId: string;
  onPageSelect: (pageId: string) => void;
  onAddPage: () => void;
  onPageNameChange: (pageId: string, newName: string) => void;
  onDeletePage: (pageId: string) => void;
  width: number;
  onResize: (deltaX: number) => void;
  onSearch?: (query: string, category: SearchCategory, results: MemoBlock[]) => void;
}

const LeftPanel: React.FC<LeftPanelProps> = ({
  pages,
  currentPageId,
  onPageSelect,
  onAddPage,
  onPageNameChange,
  onDeletePage,
  width,
  onResize,
  onSearch
}) => {
  const [editingPageId, setEditingPageId] = React.useState<string | null>(null);
  const [editingName, setEditingName] = React.useState<string>('');
  const [searchQuery, setSearchQuery] = React.useState<string>('');
  const [searchCategory, setSearchCategory] = React.useState<SearchCategory>('all');
  const [searchResults, setSearchResults] = React.useState<MemoBlock[]>([]);
  const [isSearchMode, setIsSearchMode] = React.useState<boolean>(false);
  const [showSearchFilters, setShowSearchFilters] = React.useState<boolean>(false);
  const [searchImportanceFilters, setSearchImportanceFilters] = React.useState<Set<ImportanceLevel>>(
    new Set(['critical', 'important', 'opinion', 'reference', 'question', 'idea', 'data'] as ImportanceLevel[])
  );
  const [searchShowGeneralContent, setSearchShowGeneralContent] = React.useState<boolean>(true);

  //       
  React.useEffect(() => {
    if (searchQuery.trim()) {
      const results = searchMemos(searchQuery, searchCategory);
      setSearchResults(results);
    }
  }, [searchImportanceFilters, searchShowGeneralContent]);

  const handleDoubleClick = (page: Page) => {
    setEditingPageId(page.id);
    setEditingName(page.name);
  };

  const handleEditClick = (page: Page, e: React.MouseEvent) => {
    e.stopPropagation();
    setEditingPageId(page.id);
    setEditingName(page.name);
  };

  const handleDeleteClick = (page: Page, e: React.MouseEvent) => {
    e.stopPropagation();
    if (window.confirm(`"${page.name}"   ?`)) {
      onDeletePage(page.id);
    }
  };

  const handleNameSubmit = () => {
    if (editingPageId && editingName.trim()) {
      onPageNameChange(editingPageId, editingName.trim());
    }
    setEditingPageId(null);
    setEditingName('');
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleNameSubmit();
    } else if (e.key === 'Escape') {
      setEditingPageId(null);
      setEditingName('');
    }
  };

  //     
  const normalizeText = (text: string): string => {
    return text.toLowerCase().replace(/\s+/g, '');
  };

  //    ( )
  const flexibleMatch = (text: string, query: string): boolean => {
    const normalizedText = normalizeText(text);
    const normalizedQuery = normalizeText(query);

    //      
    if (normalizedText.includes(normalizedQuery)) {
      return true;
    }

    //       
    const queryWords = query.toLowerCase().split(/\s+/).filter(word => word.length > 0);
    if (queryWords.length > 1) {
      return queryWords.every(word => text.toLowerCase().includes(word));
    }

    return false;
  };

  //      
  const getFilteredTextFromBlock = (block: any): string => {
    if (block.type !== 'text' || !block.content) return '';

    const { content, importanceRanges } = block;

    if (!importanceRanges || importanceRanges.length === 0) {
      //    
      return searchShowGeneralContent ? content : '';
    }

    //   
    const ranges = [...importanceRanges].sort((a, b) => a.start - b.start);
    let filteredText = '';
    let lastIndex = 0;

    ranges.forEach(range => {
      //   ( )
      if (range.start > lastIndex) {
        if (searchShowGeneralContent) {
          filteredText += content.substring(lastIndex, range.start);
        }
      }

      //   (  )
      if (searchImportanceFilters.has(range.level)) {
        filteredText += content.substring(range.start, range.end);
      }

      lastIndex = range.end;
    });

    //   ( )
    if (lastIndex < content.length) {
      if (searchShowGeneralContent) {
        filteredText += content.substring(lastIndex);
      }
    }

    return filteredText;
  };

  //     
  const renderSearchResultContent = (memo: MemoBlock) => {
    // blocks      
    if (memo.blocks && memo.blocks.length > 0) {
      const textBlocks = memo.blocks.filter(block => block.type === 'text' && block.content);
      if (textBlocks.length > 0) {
        const results = textBlocks.map((block, blockIndex) => {
          const textBlock = block as any;
          const { content, importanceRanges } = textBlock;

          if (!content) return null;

          //       
          const allLevels: ImportanceLevel[] = ['critical', 'important', 'opinion', 'reference', 'question', 'idea', 'data'];
          const isDefaultFilterState = searchImportanceFilters.size === allLevels.length &&
                                      allLevels.every(level => searchImportanceFilters.has(level)) &&
                                      searchShowGeneralContent;

          if (isDefaultFilterState) {
            //  :   
            return renderHighlightedText(content, importanceRanges, blockIndex);
          } else {
            //   :     
            return renderFilteredHighlightedText(content, importanceRanges, blockIndex);
          }
        }).filter(result => result !== null);

        //   ,     (  )
        return results.length > 0 ? results : [];
      }
    }

    //  content  
    if (memo.content) {
      return memo.content.length > 100 ? memo.content.substring(0, 100) + '...' : memo.content;
    }

    return ' ';
  };

  //    (  )
  const renderHighlightedText = (text: string, importanceRanges?: any[], blockIndex: number = 0) => {
    if (!importanceRanges || importanceRanges.length === 0) {
      const displayText = text.length > 200 ? text.substring(0, 200) + '...' : text;
      return (
        <span key={blockIndex}>
          {blockIndex > 0 && <br />}
          {displayText}
        </span>
      );
    }

    const ranges = [...importanceRanges].sort((a, b) => a.start - b.start);
    const parts: Array<{ text: string; level?: ImportanceLevel }> = [];
    let lastIndex = 0;
    let totalLength = 0;
    const maxLength = 100;

    ranges.forEach(range => {
      //   
      if (totalLength >= maxLength) return;

      //   ( )
      if (range.start > lastIndex) {
        const beforeText = text.substring(lastIndex, range.start);
        const remainingLength = maxLength - totalLength;
        const truncatedText = beforeText.length > remainingLength ?
                             beforeText.substring(0, remainingLength) : beforeText;
        parts.push({ text: truncatedText });
        totalLength += truncatedText.length;
      }

      //   
      if (totalLength >= maxLength) return;

      //   ( )
      const rangeText = text.substring(range.start, range.end);
      const remainingLength = maxLength - totalLength;
      const truncatedRangeText = rangeText.length > remainingLength ?
                                rangeText.substring(0, remainingLength) : rangeText;
      parts.push({
        text: truncatedRangeText,
        level: range.level
      });
      totalLength += truncatedRangeText.length;
      lastIndex = range.end;
    });

    //   ( )
    if (lastIndex < text.length && totalLength < maxLength) {
      const afterText = text.substring(lastIndex);
      const remainingLength = maxLength - totalLength;
      const truncatedText = afterText.length > remainingLength ?
                           afterText.substring(0, remainingLength) : afterText;
      parts.push({ text: truncatedText });
      totalLength += truncatedText.length;
    }

    return (
      <span key={blockIndex}>
        {blockIndex > 0 && <br />}
        {parts.map((part, index) => (
          <span
            key={index}
            style={part.level ? {
              backgroundColor: getImportanceStyle(part.level).backgroundColor,
              padding: '1px 2px',
              borderRadius: '2px',
              fontWeight: '500'
            } : {}}
          >
            {part.text}
          </span>
        ))}
        {totalLength >= maxLength ? '...' : ''}
      </span>
    );
  };

  //     (  ,   )
  const renderFilteredHighlightedText = (text: string, importanceRanges?: any[], blockIndex: number = 0) => {
    if (!importanceRanges || importanceRanges.length === 0) {
      if (searchShowGeneralContent && (!searchQuery || flexibleMatch(text, searchQuery))) {
        //  :       (  )
        const displayText = text.length > 200 ? text.substring(0, 200) + '...' : text;
        return (
          <span key={blockIndex}>
            {blockIndex > 0 && <br />}
            {displayText}
          </span>
        );
      }
      return null;
    }

    const ranges = [...importanceRanges].sort((a, b) => a.start - b.start);
    const parts: Array<{ text: string; level?: ImportanceLevel }> = [];
    let lastIndex = 0;
    let totalLength = 0;
    const maxLength = 100;
    let hasMatchingContent = false;

    ranges.forEach(range => {
      //   
      if (totalLength >= maxLength) return;

      //   ( ) -     
      if (range.start > lastIndex && searchShowGeneralContent) {
        const beforeText = text.substring(lastIndex, range.start);
        if (!searchQuery || flexibleMatch(beforeText, searchQuery)) {
          //        (  )
          const remainingLength = maxLength - totalLength;
          const truncatedText = beforeText.length > remainingLength ?
                               beforeText.substring(0, remainingLength) : beforeText;
          parts.push({ text: truncatedText });
          totalLength += truncatedText.length;
          hasMatchingContent = true;
        }
      }

      //   (   -     )
      if (searchImportanceFilters.has(range.level)) {
        const rangeText = text.substring(range.start, range.end);

        //        
        if (!searchQuery || flexibleMatch(rangeText, searchQuery)) {
          //   
          if (totalLength >= maxLength) return;

          const remainingLength = maxLength - totalLength;
          const truncatedRangeText = rangeText.length > remainingLength ?
                                    rangeText.substring(0, remainingLength) : rangeText;
          parts.push({
            text: truncatedRangeText,
            level: range.level
          });
          totalLength += truncatedRangeText.length;
          hasMatchingContent = true;
        }
      }

      lastIndex = range.end;
    });

    //   ( ) -     
    if (lastIndex < text.length && totalLength < maxLength && searchShowGeneralContent) {
      const afterText = text.substring(lastIndex);
      if (!searchQuery || flexibleMatch(afterText, searchQuery)) {
        //        (  )
        const remainingLength = maxLength - totalLength;
        const truncatedText = afterText.length > remainingLength ?
                             afterText.substring(0, remainingLength) : afterText;
        parts.push({ text: truncatedText });
        totalLength += truncatedText.length;
        hasMatchingContent = true;
      }
    }

    //    null  (   )
    if (parts.length === 0 || !hasMatchingContent) {
      return null;
    }

    return (
      <span key={blockIndex}>
        {blockIndex > 0 && <br />}
        {parts.map((part, index) => (
          <span
            key={index}
            style={part.level ? {
              backgroundColor: getImportanceStyle(part.level).backgroundColor,
              padding: '1px 2px',
              borderRadius: '2px',
              fontWeight: '500'
            } : {}}
          >
            {part.text}
          </span>
        ))}
        {totalLength >= maxLength ? '...' : ''}
      </span>
    );
  };

  //  
  const searchMemos = (query: string, category: SearchCategory): MemoBlock[] => {
    if (!query.trim()) {
      return [];
    }

    const allMemos: MemoBlock[] = pages.flatMap(page => page.memos || []);

    return allMemos.filter(memo => {
      switch (category) {
        case 'title':
          return flexibleMatch(memo.title, query);
        case 'tags':
          return memo.tags?.some(tag => flexibleMatch(tag, query)) || false;
        case 'content':
          //  content  (  )
          if (memo.content && flexibleMatch(memo.content, query)) {
            return true;
          }
          // blocks   (  )
          return memo.blocks?.some(block => {
            if (block.type === 'text') {
              const filteredContent = getFilteredTextFromBlock(block);
              return filteredContent && flexibleMatch(filteredContent, query);
            }
            return false;
          }) || false;
        case 'all':
        default:
          //  
          if (flexibleMatch(memo.title, query)) return true;
          //  
          if (memo.tags?.some(tag => flexibleMatch(tag, query))) return true;
          //   (  )
          if (memo.content && flexibleMatch(memo.content, query)) return true;
          // blocks   (  )
          return memo.blocks?.some(block => {
            if (block.type === 'text') {
              const filteredContent = getFilteredTextFromBlock(block);
              return filteredContent && flexibleMatch(filteredContent, query);
            }
            return false;
          }) || false;
      }
    });
  };

  const handleSearch = (query: string) => {
    setSearchQuery(query);
    if (query.trim()) {
      const results = searchMemos(query, searchCategory);
      setSearchResults(results);
      setIsSearchMode(true);
      if (onSearch) {
        onSearch(query, searchCategory, results);
      }
    } else {
      setSearchResults([]);
      setIsSearchMode(false);
    }
  };

  const handleCategoryChange = (category: SearchCategory) => {
    setSearchCategory(category);
    if (searchQuery.trim()) {
      handleSearch(searchQuery);
    }
  };

  const clearSearch = () => {
    setSearchQuery('');
    setSearchResults([]);
    setIsSearchMode(false);
  };

  return (
    <div style={{
      width: `${width}px`,
      backgroundColor: '#ffffff',
      color: '#1f2937',
      padding: '24px',
      borderRight: '1px solid #e5e7eb',
      position: 'relative'
    }}>
      <div style={{ marginBottom: '32px' }}>
        <h2 style={{ margin: '0 0 20px 0', fontSize: '24px', fontWeight: '700', color: '#1f2937' }}></h2>

        {/*  UI */}
        <div style={{ marginBottom: '16px' }}>
          <div style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: '8px',
            gap: '8px'
          }}>
            <input
              type="text"
              placeholder=" ..."
              value={searchQuery}
              onChange={(e) => handleSearch(e.target.value)}
              style={{
                flex: 1,
                padding: '8px 12px',
                border: '1px solid #d1d5db',
                borderRadius: '6px',
                fontSize: '14px',
                outline: 'none',
                transition: 'border-color 0.2s ease'
              }}
              onFocus={(e) => {
                e.currentTarget.style.borderColor = '#3b82f6';
              }}
              onBlur={(e) => {
                e.currentTarget.style.borderColor = '#d1d5db';
              }}
            />

            {searchQuery && (
              <button
                onClick={clearSearch}
                style={{
                  backgroundColor: '#f3f4f6',
                  color: '#6b7280',
                  border: 'none',
                  borderRadius: '4px',
                  width: '24px',
                  height: '24px',
                  cursor: 'pointer',
                  fontSize: '12px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center'
                }}
              >
                
              </button>
            )}
          </div>

          {/*    -    */}
          <div style={{ display: 'flex', justifyContent: 'flex-start', marginBottom: '8px' }}>
            <button
              onClick={() => setShowSearchFilters(!showSearchFilters)}
              style={{
                backgroundColor: showSearchFilters ? '#3b82f6' : '#f8f9fa',
                color: showSearchFilters ? 'white' : '#6b7280',
                border: '1px solid #e5e7eb',
                borderRadius: '6px',
                padding: '6px 10px',
                cursor: 'pointer',
                fontSize: '12px',
                display: 'flex',
                alignItems: 'center',
                gap: '6px',
                transition: 'all 0.2s ease'
              }}
            >
              {/*     () */}
              <svg width="14" height="14" viewBox="0 0 14 14" fill="none" stroke="currentColor" strokeWidth="1.5">
                <path d="M1 2h12l-4 6v4l-4-2v-2L1 2z" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
              
            </button>
          </div>

          {/*   -   */}
          {showSearchFilters && (
            <div style={{
              padding: '12px',
              backgroundColor: '#f8f9fa',
              borderRadius: '6px',
              marginBottom: '8px'
            }}>
              {/*    */}
              <div style={{ marginBottom: '12px' }}>
                <span style={{ fontSize: '12px', fontWeight: '600', color: '#374151', marginBottom: '4px', display: 'block' }}>
                   
                </span>
                <div style={{
                  display: 'flex',
                  gap: '4px',
                  flexWrap: 'wrap'
                }}>
                  {['all', 'title', 'tags', 'content'].map((category) => (
                    <button
                      key={category}
                      onClick={() => handleCategoryChange(category as SearchCategory)}
                      style={{
                        padding: '4px 8px',
                        fontSize: '12px',
                        backgroundColor: searchCategory === category ? '#3b82f6' : '#ffffff',
                        color: searchCategory === category ? 'white' : '#6b7280',
                        border: '1px solid #e5e7eb',
                        borderRadius: '4px',
                        cursor: 'pointer',
                        transition: 'all 0.2s ease'
                      }}
                    >
                      {category === 'all' ? '' :
                       category === 'title' ? '' :
                       category === 'tags' ? '' : ''}
                    </button>
                  ))}
                </div>
              </div>

              {/*    -     */}
              {(searchCategory === 'content' || searchCategory === 'all') && (
                <div>
                  <span style={{ fontSize: '12px', fontWeight: '600', color: '#374151', marginBottom: '4px', display: 'block' }}>
                     
                  </span>

                  {/*    */}
                  <div style={{ marginBottom: '8px' }}>
                    <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer', fontSize: '12px' }}>
                      <input
                        type="checkbox"
                        checked={searchShowGeneralContent}
                        onChange={(e) => {
                          setSearchShowGeneralContent(e.target.checked);
                        }}
                        style={{ marginRight: '6px' }}
                      />
                      <span style={{ color: '#6b7280' }}> </span>
                    </label>
                  </div>

                  {/*    */}
                  <div style={{
                    display: 'flex',
                    gap: '4px',
                    flexWrap: 'wrap',
                    marginBottom: '8px'
                  }}>
                    {([
                      { level: 'critical', label: ' ', color: '#ffcdd2' },
                      { level: 'important', label: ' ', color: '#ffcc80' },
                      { level: 'opinion', label: ' ', color: '#e1bee7' },
                      { level: 'reference', label: ' ', color: '#81d4fa' },
                      { level: 'question', label: ' ', color: '#fff59d' },
                      { level: 'idea', label: ' ', color: '#c8e6c9' },
                      { level: 'data', label: ' ', color: '#ffab91' }
                    ] as Array<{level: ImportanceLevel, label: string, color: string}>).map(({level, label, color}) => (
                      <button
                        key={level}
                        onClick={() => {
                          const newFilters = new Set(searchImportanceFilters);
                          if (newFilters.has(level)) {
                            newFilters.delete(level);
                          } else {
                            newFilters.add(level);
                          }
                          setSearchImportanceFilters(newFilters);
                        }}
                        style={{
                          padding: '4px 8px',
                          fontSize: '11px',
                          backgroundColor: searchImportanceFilters.has(level) ? color : '#ffffff',
                          color: searchImportanceFilters.has(level) ? '#000' : '#6b7280',
                          border: '1px solid #e5e7eb',
                          borderRadius: '4px',
                          cursor: 'pointer',
                          transition: 'all 0.2s ease'
                        }}
                      >
                        {label}
                      </button>
                    ))}
                  </div>

                  {/*  /  */}
                  <div style={{ display: 'flex', gap: '4px' }}>
                    <button
                      onClick={() => {
                        setSearchImportanceFilters(new Set(['critical', 'important', 'opinion', 'reference', 'question', 'idea', 'data'] as ImportanceLevel[]));
                      }}
                      style={{
                        padding: '2px 6px',
                        fontSize: '10px',
                        backgroundColor: '#f3f4f6',
                        color: '#6b7280',
                        border: '1px solid #e5e7eb',
                        borderRadius: '3px',
                        cursor: 'pointer'
                      }}
                    >
                       
                    </button>
                    <button
                      onClick={() => {
                        setSearchImportanceFilters(new Set());
                      }}
                      style={{
                        padding: '2px 6px',
                        fontSize: '10px',
                        backgroundColor: '#f3f4f6',
                        color: '#6b7280',
                        border: '1px solid #e5e7eb',
                        borderRadius: '3px',
                        cursor: 'pointer'
                      }}
                    >
                       
                    </button>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
      
      {/*    */}
      {isSearchMode && (
        <div style={{ marginBottom: '24px' }}>
          <div style={{ display: 'flex', alignItems: 'center', marginBottom: '16px' }}>
            <h3 style={{ margin: 0, flex: 1, fontSize: '18px', fontWeight: '600', color: '#374151' }}>
                ({searchResults.length})
            </h3>
          </div>
          <div>
            {searchResults.length > 0 ? (
              searchResults.map(memo => {
                const parentPage = pages.find(p => p.memos?.some(m => m.id === memo.id));
                return (
                  <div
                    key={memo.id}
                    onClick={() => {
                      if (parentPage) {
                        onPageSelect(parentPage.id);
                      }
                    }}
                    style={{
                      padding: '12px 16px',
                      marginBottom: '8px',
                      backgroundColor: '#f8fafc',
                      border: '1px solid #e2e8f0',
                      borderRadius: '8px',
                      cursor: 'pointer',
                      transition: 'all 0.2s ease',
                      fontSize: '13px'
                    }}
                    onMouseOver={(e) => {
                      e.currentTarget.style.backgroundColor = '#f1f5f9';
                    }}
                    onMouseOut={(e) => {
                      e.currentTarget.style.backgroundColor = '#f8fafc';
                    }}
                  >
                    <div style={{ fontWeight: '600', color: '#1e293b', marginBottom: '4px' }}>
                      {memo.title || ' '}
                    </div>
                    <div style={{ fontSize: '11px', color: '#64748b', marginBottom: '4px' }}>
                       {parentPage?.name || ' '}
                    </div>
                    {memo.tags && memo.tags.length > 0 && (
                      <div style={{ display: 'flex', gap: '4px', flexWrap: 'wrap', marginBottom: '4px' }}>
                        {memo.tags.map(tag => (
                          <span key={tag} style={{
                            padding: '2px 6px',
                            backgroundColor: '#3b82f6',
                            color: 'white',
                            borderRadius: '3px',
                            fontSize: '10px'
                          }}>
                            {tag}
                          </span>
                        ))}
                      </div>
                    )}
                    <div style={{ color: '#475569', fontSize: '11px', lineHeight: '1.3' }}>
                      {(() => {
                        const content = renderSearchResultContent(memo);
                        //   null  " " 
                        if (Array.isArray(content) && content.length === 0) {
                          return ' ';
                        }
                        return content;
                      })()}
                    </div>
                  </div>
                );
              })
            ) : (
              <div style={{
                padding: '20px',
                textAlign: 'center',
                color: '#6b7280',
                fontSize: '14px'
              }}>
                  .
              </div>
            )}
          </div>
        </div>
      )}

      {/*   */}
      <div style={{ marginBottom: '24px' }}>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '16px' }}>
          <h3 style={{ margin: 0, flex: 1, fontSize: '18px', fontWeight: '600', color: '#374151' }}>
            
          </h3>
          <button
            onClick={onAddPage}
            style={{
              backgroundColor: '#f3f4f6',
              color: '#6b7280',
              border: 'none',
              borderRadius: '6px',
              width: '24px',
              height: '24px',
              cursor: 'pointer',
              fontSize: '14px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              transition: 'all 0.2s ease'
            }}
            onMouseOver={(e) => {
              e.currentTarget.style.backgroundColor = '#e5e7eb';
            }}
            onMouseOut={(e) => {
              e.currentTarget.style.backgroundColor = '#f3f4f6';
            }}
          >
            +
          </button>
        </div>
        <div>
          {pages.map(page => (
          <div
            key={page.id}
            onClick={() => onPageSelect(page.id)}
            onDoubleClick={() => handleDoubleClick(page)}
            style={{
              padding: '12px 16px',
              marginBottom: '8px',
              backgroundColor: currentPageId === page.id ? '#f3f4f6' : 'transparent',
              border: 'none',
              borderRadius: '8px',
              cursor: 'pointer',
              transition: 'all 0.2s ease',
              fontSize: '14px',
              fontWeight: '500',
              display: 'flex',
              alignItems: 'flex-start',
              justifyContent: 'space-between',
              gap: '8px',
              minHeight: '20px'
            }}
            onMouseOver={(e) => {
              if (currentPageId !== page.id) {
                e.currentTarget.style.backgroundColor = '#f9fafb';
              }
            }}
            onMouseOut={(e) => {
              if (currentPageId !== page.id) {
                e.currentTarget.style.backgroundColor = 'transparent';
              }
            }}
          >
            <div style={{ 
              flex: 1, 
              minWidth: 0,
              marginRight: '8px'
            }}>
              {editingPageId === page.id ? (
                <input
                  type="text"
                  value={editingName}
                  onChange={(e) => setEditingName(e.target.value)}
                  onBlur={handleNameSubmit}
                  onKeyDown={handleKeyPress}
                  autoFocus
                  style={{
                    backgroundColor: 'transparent',
                    border: 'none',
                    color: '#374151',
                    width: '100%',
                    outline: 'none',
                    fontSize: '14px'
                  }}
                />
              ) : (
                <span style={{ 
                  display: 'block',
                  wordBreak: 'break-word',
                  whiteSpace: 'normal',
                  lineHeight: '1.4',
                  fontSize: '14px'
                }}>
                  {page.name}
                </span>
              )}
            </div>
            
            {editingPageId !== page.id && (
              <div style={{ 
                display: 'flex', 
                gap: '4px',
                flexShrink: 0,
                paddingTop: '2px'
              }}>
                {/*   */}
                <button
                  onClick={(e) => handleEditClick(page, e)}
                  style={{
                    background: 'none',
                    border: 'none',
                    cursor: 'pointer',
                    padding: '4px',
                    borderRadius: '4px',
                    color: '#6b7280',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    transition: 'all 0.2s ease'
                  }}
                  onMouseOver={(e) => {
                    e.currentTarget.style.backgroundColor = '#e5e7eb';
                    e.currentTarget.style.color = '#374151';
                  }}
                  onMouseOut={(e) => {
                    e.currentTarget.style.backgroundColor = 'transparent';
                    e.currentTarget.style.color = '#6b7280';
                  }}
                  title="  "
                >
                  <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"/>
                    <path d="m18.5 2.5 3 3L12 15l-4 1 1-4z"/>
                  </svg>
                </button>
                
                {/*   */}
                <button
                  onClick={(e) => handleDeleteClick(page, e)}
                  style={{
                    background: 'none',
                    border: 'none',
                    cursor: 'pointer',
                    padding: '4px',
                    borderRadius: '4px',
                    color: '#6b7280',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    transition: 'all 0.2s ease'
                  }}
                  onMouseOver={(e) => {
                    e.currentTarget.style.backgroundColor = '#fef2f2';
                    e.currentTarget.style.color = '#ef4444';
                  }}
                  onMouseOut={(e) => {
                    e.currentTarget.style.backgroundColor = 'transparent';
                    e.currentTarget.style.color = '#6b7280';
                  }}
                  title=" "
                >
                  <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                    <path d="M3 6h18"/>
                    <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"/>
                    <path d="M8 6V4c0-1 1-2 2-2h4c0 1 1 2 2 2v2"/>
                    <line x1="10" y1="11" x2="10" y2="17"/>
                    <line x1="14" y1="11" x2="14" y2="17"/>
                  </svg>
                </button>
              </div>
            )}
          </div>
        ))
          }
        </div>
      </div>

      <Resizer direction="left" onResize={onResize} />
    </div>
  );
};

export default LeftPanel;